            </div>
        </div>
        <div class="banner-content clearfix">
            <div class="banner-box fl">
                <div class="block">
                    <el-carousel height="400px">
                    <el-carousel-item v-for="(item,index) in HostnewsData.slice(0,6)" :key="index" >
                        <img class="imgs auto-img" :src="item.images" alt="" @click="goDetail(item.id)">
                    </el-carousel-item>
                    </el-carousel>
                </div>
            </div>
            <div class="cart-box fl ">
                <div class="cart-item" v-for="(item,index) in HostnewsData.slice(8,10)" :key="index" >
                    <img class="imgs" :src="item.images" alt="" @click="goDetail(item.id)">
                    <div class="text">{{item.title}}</div>
                </div>
            </div>
        </div>
        <div class="newList-content">
            <div class="clearfix">
                <div class="new-list-left-box fl">
                     <div class="list-item clearfix" v-for="(item,index) in HostnewsData" :key="index" @click="goDetail(item.id)">
                        <div class="left-img fl">
                            <el-image class=" err-imgs">
                                <div slot="error" class="image-slot">
                                    <i class="el-icon-picture-outline"></i>
                                </div>
                            </el-image>
                            <img class="a-img auto-img" :src="item.images" alt="">
                        </div>
                        <div class="right-cont fl">
                            <div class="title">{{item.title}}</div>
                            <div class="text-cont">
                                <span class="post-name">{{item.posterNickName}}</span>
                                <span class="pub-time">日期：{{item.publishTime}}</span>
                                <span class="comment-count">评论：{{item.commentCount}}</span>
                            </div>
                        </div>
                    </div>

                     <div class="pagination-page">
                        <el-pagination 
                        class="pag-box" 
                        background 
                        layout="prev, pager, next" 
                        :size-change="10"
                        :total="1000"
                        :current-change = "currentPage"
                        @current-change="handleCurrentChange"
                        >
                        </el-pagination>
                    </div>
                   
                </div>

                <div class="new-other-right-box fl">
                    <div class="other-list">
                        <div class="other-box-one">
                            <div class="other-title">
                                <div class="ot-name">2020全国两会</div>
                            </div>
                            <img class="imgs auto-img" src="http://www.81.cn/attachement/jpg/site351/20200303/0800279e7f7f1fc839f401.jpg" alt="">
                            <ul>
                                <li><a href="http://news.163.com/special/zghj70/" target="_blank" >中国海军成立70周年</a></li>
                                <li><a href="http://news.cctv.com/2020/08/12/ARTIed4YPzWmOpFQ27EUnYav200812.shtml" target="_blank" >全面建成小康</a></li>
                                <li><a href="http://news.cctv.com/special/2020lianghui/index.shtml" target="_blank" >奋战决胜之年</a></li>
                                <li><a href="http://news.cctv.com/special/nhdbwy/lhxgc/index.shtml" target="_blank" >两会新观察</a></li>
                                <li><a href="https://news.cctv.com/2020/08/18/ARTI5Uy4EqnSfEIfygzulqiG200818.shtml?spm=C94212.P4YnMod9m2uD.ENPMkWvfnaiV.5" target="_blank" >走进我们的小康生活</a></li>
                            </ul>
                        </div>
                        <div class="other-box-two">
                            <div class="two-content">
                                <div class="other-title">
                                <div class="ot-name">公益在行动</div>
                            </div>
                            <img class="gy-img" src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=597754428,3095897263&fm=26&gp=0.jpg" alt="">
                            <div class="texts">保护穿山甲，我们在行动。我们同事地球的生命，一起守护家园。</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import "../../assets/css/hostnews.less";
    export default {
        name:'hostnews',
        data(){
            return{
                currentPage:1,
                // 定义页数
                pageNum:'',
                HostnewsData:[],
            }
        },
        created(){
            this.getHostnewsData()
        },
        methods:{

            getHostnewsData(pageNum){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1467284926140',
                        page:pageNum,
                    }
                }).then((result) =>{
                    // console.log('result',result)
                    this.HostnewsData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },
             //点击页码，获取对应的val值，在传给要显示的页数内容
            handleCurrentChange(val){
                
                // console.log('val',val)
                let pageNum = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getHostnewsData(pageNum)
            
            },
            goDetail(id) {
                // console.log(id);
                this.$router.push({ name: "Detail", query: { id } });
            },
        }
    }
</script>

